ame: 'Collector',\n\tcomponents: {\n\t\tTasksNew,\n\t\tTasksList,\n\t},\n\tprops: {\n\t\trootTitle: {\n\t\t\ttype: String,\n\t\t\trequired: true,\n\t\t},\n\t\tloading: {\n\t\t\ttype: Boolean,\n\t\t\trequired: true,\n\t\t},\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tupdating: false,\n\t\t}\n\t},\n\tbeforeMount() {\n\t\tif (this.getTasks().length > 0) {\n\t\t\tthis.$emit('update:loading', false)\n\t\t} else {\n\t\t\tconst tasks = loadState('mediadc', 'tasks', false)\n\t\t\tconst settings = loadState('mediadc', 'settings', false)\n\t\t\tif (tasks) {\n\t\t\t\tthis.$store.commit('setTasks', tasks)\n\t\t\t\tthis.$emit('update:loading', false)\n\t\t\t\tif (settings) {\n\t\t\t\t\tthis.$store.commit('setSettings', settings)\n\t\t\t\t\tthis.$emit('update:loading', false)\n\t\t\t\t} else {\n\t\t\t\t\tthis.$store.dispatch('getSettings').then(() => {\n\t\t\t\t\t\tthis.$emit('update:loading', false)\n\t\t\t\t\t})\n\t\t\t\t}\n\t\t\t} else {\n\t\t\t\tthis.$store.dispatch('getTasks', true).then(() => {\n\t\t\t\t\tthis.$store.dispatch('getSettings').then(() => {\n\t\t\t\t\t\tthis.$emit('update:loading', false)\n\t\t\t\t\t})\n\t\t\t\t})\n\t\t\t}\n\t\t}\n\t},\n\tmethods: {\n\t\t...mapActions(['getTasks', 'getSettings']),\n\t},\n}\n</script>\n\n<style scoped>\n.container {\n\tpadding: 0 20px;\n\twidth: 100%;\n\tmax-width: 1440px;\n\tmargin: 0 auto;\n}\n\nh2 {\n\tfont-size: 24px;\n\tfont-weight: bold;\n\ttext-align: center;\n\tmargin: 20px 0 10px;\n}\n\np {\n\tpadding: 0 10px;\n\tmargin: 10px 0;\n\ttext-align: center;\n}\n\n.mediadc-row {\n\tdisplay: flex;\n\tjustify-content: center;\n}\n\n@media (max-width: 960px) {\n\t.mediadc-row {\n\t\tflex-wrap: wrap;\n\t}\n}\n</style>\n"],"sourceRoot":""}]);
